body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  height:100%;
}
/* 
    BREAKS POINTS
    xs: '480px',
    sm: '768px',
    md: '992px',
    lg: '1200px',
    xl: '1600px',
*/
/* --------------------------------------------------------------------------------- */
/* ---------- XS SIZE ------------- */

/* CONNEXION */
#connexion {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* LOADING */
#load {
  margin: 2em auto;
}

#load > h3 {
  text-align: center;
  color: #009688;
}

/*
BUTTONS
*/
.ant-btn {
    color: aliceblue;
    margin : 1em 0;
    font-size: 0.8em;
    background-color: #009688;
    border-color: #004D40;
    border-radius: 7px;
    text-shadow: 0 -1px 0 rgba(236, 232, 232, 0.12);
    box-shadow: 0 2px 0 rgba(240, 237, 237, 0.045);
}

.ant-btn:hover, .ant-btn:focus {
    background: #078073;
    border: 1px solid #009688;
}

.ant-btn-default {
  color: black;
  background-color: #eee;
}

.ant-btn-default:hover, .ant-btn-default:focus {
  color: aliceblue;
}

.trash {
    background: transparent;
    border: none;
    font-size: 1em;
    margin: 0.8em 0;
    cursor: pointer;
}

.trash:hover, .trash:focus {
    background: transparent;
    border: none;
}

button {
  background: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  width: 50px;
}

.form > button {
  -webkit-transition: 0.5s;
  transition: 0.5s;
  font-size: 0.9em;
  padding: 0;
}

#buttonSubmit {
  border-radius: 50%;
}

.formButton-active {
  background: #26A69A;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

/* bouton valider dans profil */
.changeUid {
  margin-left: 1em;
  width: 120px;
}

/*
HEADER
*/
.ant-layout-header {
    border-radius: 0 0 7px 7px;
    background: #009688;
    display: flex;
    font-size: 1em;
    height: 80px;
}

.ant-layout-header i {
  color: aliceblue;
  border: 1px solid aliceblue;
  padding: 0.5em;
  border-radius: 50px;
  
}

.ant-layout-header h4 {
  margin-left: 1.5em;
  color: aliceblue;
  width: 100%;
  padding-top: 10px;
}

/* menu */
.ant-layout-header .ant-menu {
  background: #009688;
  border: none;
  padding-top: 10px;
}

.ant-layout-header .ant-menu .ant-menu-item i {
  font-size: 0.9em;
}

.ant-menu-horizontal > .ant-menu-item:hover, .ant-menu-horizontal > .ant-menu-submenu:hover, .ant-menu-horizontal > .ant-menu-item-active, .ant-menu-horizontal > .ant-menu-submenu-active, .ant-menu-horizontal > .ant-menu-item-open, .ant-menu-horizontal > .ant-menu-submenu-open, .ant-menu-horizontal > .ant-menu-item-selected, .ant-menu-horizontal > .ant-menu-submenu-selected {
  border-bottom: 1px solid #fff;
}

/* 
CONTENT
*/
/* logo */
#bckLogo {
  min-height: 16em;
}

#bckLogo > i {
  color: #009688;
  border: 6px solid #009688;
  border-radius: 100px;
  font-size: 120px;
  margin-left: calc(50% - 86px);
  margin-top: 20px;
  padding: 20px;
}

#bckLogo > h2 {
  color: #009688;
  font-weight: bold;
  font-size: 2.5em;
  margin-top: 0.4em;
}

#bckLogo2 > i {
  color: #009688;
  border-radius: 100px;
  font-size: 80px;
  border: 4px solid #009688;
  margin-left: calc(50% - 58px);
  margin-top: 20px;
  padding: 14px;
}

#bckLogo2 > h2 {
  color: #009688;
  font-weight: bold;
  font-size: 1em;
  margin-top: 0.2em;
}

.ant-layout-content {
    /* margin-bottom: 5em; */
    font-size: 1em;
    text-align: justify;
    padding: 1em;
    min-height: calc(100vh - 140px);
}

.ant-layout-content h2, h3 {
    text-align: center;
    font-weight: bold;
}

.ant-layout-content .mySwitchType {
  text-align: center;
}

.ant-layout-content .mySwitchType i {
  font-size: 1.5em;
  margin: 1em 0.7em;
}

.ant-layout-content .mySwitchType button {
  margin-bottom: 0.5em;
  background: #009688;
}

/* list items en mode liste */
.ant-list {
  margin-bottom: 1em;
  box-shadow: 5px 5px 8px #a8bbb9;
}
.ant-list-header {
  background: #009688;
  color: white;
  font-weight: bold;
  padding: 0;
  border-radius: 5px 5px 0 0;
  cursor: pointer;
}

.ant-list-bordered .ant-list-item {
  padding: 0;
  background: #fff;
  border-bottom: 1px solid #009688;
  cursor: pointer;
}

.ant-list-items-item {
  padding-left: 1.5em;
  padding-top: 1.1em;
  color: rgb(26, 25, 25);
  height: 54.4px;
}

.form-comm-container {
  height: 0;
}

.form-comm-container-active {
  min-height: 10px;
}

/*
FOOTER
*/
.ant-layout-footer {
    border-radius: 7px 7px 0 0;
    background: #009688;
    color: aliceblue;
    padding: 1.3em;
    height: 60px;
}

.ant-layout-footer p {
    text-align: center;
}

/* 
FORM
*/

.form {
    background: #009688;
    border: 1px solid #004D40;
    border-width: 0 0 3px 0;
    border-radius: 50px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.21);
    display: flex;
    font-size: 1.5em;
    height: 2.5em;
    margin-bottom: 0.5em;
  }

  .form > * {
    border: none;
    color: #fff;
    font-family: 'Roboto', sans-serif; 
    outline: none;
    padding: 1em;
  }

  .form > input {
    width: 100%;
    font-size: 0.9em;
  }
  
  .form > input::-webkit-input-placeholder {
    color: #B2DFDB;
  }
  
  .form > input::-ms-input-placeholder {
    color: #B2DFDB;
  }
  
  .form > input::placeholder {
    color: #B2DFDB;
  }

  .form-titre {
    background: #078073;
    margin: 1em auto;
    width: 100%;
    border-radius: 50px;
  }
  
  .form-titre > * {
    background: #078073;
    border-radius: 50px;
  }

  .form > button {
    -webkit-transition: 0.5s;
    transition: 0.5s;
    font-size: 0.9em;
  }

  /* SWITCH */
.ant-switch, .ant-switch-checked {
    border: 1px solid #078073 ;
    font-size: 14px;
    line-height: 1.5;
    color: rgba(0, 0, 0, 0.65);
    margin: auto 0 auto 0.5em;
    padding: 0;
    list-style: none;
    position: relative;
    display: inline-block;
    box-sizing: border-box;
    height: 24px;
    min-width: 44px;
    line-height: 20px;
    vertical-align: middle;
    background-color: rgba(34, 32, 32, 0.25);
    cursor: pointer;
    -webkit-transition: all 0.36s;
    transition: all 0.36s;
    align-content: center;
  }

  /* Tooltip */
.ant-tooltip-placement-top .ant-tooltip-arrow, .ant-tooltip-placement-topLeft .ant-tooltip-arrow, .ant-tooltip-placement-topRight .ant-tooltip-arrow {
    bottom: 3px;
    border-width: 5px 5px 0;
    border-top-color: #009688;
  }
  
  .ant-tooltip-inner  {
    background-color: #009688;
  }
  
  /* 
    COLLAPSE 
  */
  
  .ant-collapse {
      margin-bottom: 1em;
      box-shadow: 5px 5px 8px #a8bbb9;
  }

  /* Header du collapse */
  .ant-collapse > .ant-collapse-item > .ant-collapse-header {
    background: #009688;
    padding-top: 0;
    color: aliceblue;
    font-weight: bold;
    padding-bottom: 0;
  }

  /* Ligne qui contient l'item, le bouton trash et commentaire */
  .ant-row-flex .ant-row-flex-space-between .ant-row-flex-middle {
    border: 1px solid #009688;
    border-left: none;
    border-radius: 7px;
    margin-left: -4px;
    position:relative;
    overflow:hidden;
    z-index:1;
    padding-left: 2em;
    padding-bottom: 0.7em;
    height: 50px;
    font-weight: bold;
    font-size: 1em;
  }

  /* Arrondi de la div */
  .ant-row-flex .ant-row-flex-space-between .ant-row-flex-middle:before {
    content:'';
    position:absolute;
    top:0;
    bottom:0;
    }

    .ant-row-flex .ant-row-flex-space-between .ant-row-flex-middle:before {
      top:-3px;
      bottom:-3px;
      border-radius:50px;
      border:1px solid #009688;
      width:56px;
      z-index:-1;
      box-shadow:0 0 0 30px transparent;
    }
    .ant-row-flex .ant-row-flex-space-between .ant-row-flex-middle:before {
      left:-2.9em;
    }

    /* ne marche pas */
    .ant-row-flex .ant-row-flex-space-between .ant-row-flex-middle .flip:before {
      box-shadow:0 0 0 30px #ECEFF1;
    }

  /* Div check */
  #check > button {
    height: 45px;
    width: 45px;
    z-index: 2;
  }

  /* Bouton check */
  .checked {
    border: 1px solid #009688;
    border-radius: 50px;
    margin: 0;
    background: transparent;
    color: #009688;
    /* height: 60px;
    width: 60px; */
  }

  .checked i {
    font-size: 1.6em;
    font-weight: bold;
  }

  .checked:hover {
    background: transparent;
    color: #078073;
    border: 2px solid #078073;
  }

  .checked:focus {
    background: transparent;
    color: #078073;
  }

  .isChecked {
    border: 1px solid #009688;
    border-radius: 50px;
    margin: 0;
    font-size: 1.8em;
    font-weight: bold;
    background: #009688;
    color: aliceblue;
  }

  .isChecked i {
    font-size: 1.6em;
    font-weight: bold;
  }

  .isChecked:hover {
    background: #349187;
    border: 2px solid aliceblue;
  }

  .isChecked:focus {
    background: #349187;
  }

  /* Formulaire de commentaire */
  .form-comm {
    margin-top: -1em;
  }

  .form-comm > input {
    box-sizing: border-box;
    width: 100%;
    font-size: 0.9em;
    padding: 0.6em;
    /* margin-bottom: 0.5em; */
    border: none;
    /* border-top: 1px solid rgba(0, 0, 0, 0.21); */
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.21);
    background: #0f96882d;
  }

  .message-enter-active {
    margin-top: 1.1em;
  }

/*
  ANIMATION
*/
.flipCollapse {
  background: #ECEFF1;
  color: rgb(148, 147, 147);
  -webkit-transform: rotateX(360deg);
          transform: rotateX(360deg);
}

.flipCollapse > div {
  text-decoration: line-through;
}

.flip > div {
    background: #ECEFF1;
    color: rgb(148, 147, 147);
    -webkit-transform: rotateX(360deg);
            transform: rotateX(360deg);
  }
  .flip > div > div > div > div{
    text-decoration: line-through;
  }
  
  .hide {
    display: none;
  }
  
  /*
    FIREBASE-UI
  */
  .container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #0288D1;
    background: -webkit-radial-gradient(circle, #039BE5, #01579b);
    background: radial-gradient(circle, #039BE5, #01579b);
    z-index: 10000;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    flex-direction: column;
    font-family: sans-serif;
  }
  .signedIn {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .logo {
    font-family: 'Amaranth', sans-serif;
    font-size: 200%;
  }
  .logoIcon {
    top: 4px;
    font-size: 32px;
    margin-right: -2px;
    position: relative;
  }
  .caption {
    margin: 20px 0 40px 0;
    font-family: 'Amaranth', sans-serif;
    font-size: 18px;
    opacity: 0.8;
  }
  .skip {
    font-weight:lighter;
    color:white;
    opacity: 0.7;
    width: 100%;
    display: block;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
  }
  .skip:HOVER {
    text-decoration: underline;
  }
  .firebaseUi {
    min-width: 250px;
  }
  .button {
    cursor: pointer;
    color: blue;
    text-decoration: underline;
    margin-top: 10px;
  }
.message-enter {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
    -webkit-transition: opacity 800ms ease-in;
    transition: opacity 800ms ease-in;
}

.message-enter-active {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
    -webkit-transition: all 0.6s;
    transition: all 0.6s;
    width:auto;
    height:auto;
}

.message-leave {
    opacity: 0.01;
    -webkit-transition: opacity 380ms ease-in;
    transition: opacity 380ms ease-in;
}

.message-leave-active {
    opacity:0;
    -webkit-transition: all 1s;
    transition: all 1s;
    width:0;
    height:0;
    overflow:hidden;
    box-sizing:border-box;
    position:relative;   
    -webkit-transform: translateY(0);   
            transform: translateY(0); 
}

.box {
    opacity: 1;
    -webkit-transition: opacity 1s, height 0;
    transition: opacity 1s, height 0;
}
    
.hidden {
    opacity: 0;
    -webkit-transition: opacity 2s, height 0 2s;
    transition: opacity 2s, height 0 2s;
    height: 0;
}
